/* Section 1 */

.main {
    padding: 6rem 0;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(277px, 1fr));
    row-gap: 2rem;
    column-gap: 6rem;
    align-items: center;
}
.main h3 {
    color: #fff;
    font-size: 1.5rem;
    opacity: 0;
    animation: topSideAni 1s ease forwards;
}
.main h1 {
    color: #fff;
    font-size: 4rem;
    font-weight: 600;
    opacity: 0;
    animation: leftSideAni 1s ease forwards;
    animation-delay: 1s;
}
.resume a[target="_blank"] {
    background-color: transparent;
    color:crimson;
    border: 2px solid rgb(69, 78, 160);
    padding: 0.4rem 1rem;
    font-size: 1.1rem;
    border-radius: 10px;
 
    cursor: pointer;
    text-decoration: none;
}
 .social-links a[target="_blank"]{
 
  color:crimson;
    border: 2px solid rgb(69, 78, 160);
    padding: 0.4rem 1rem;
    border-radius: 10px;
 justify-content:start;
    
}
.images {
    margin-left: auto;
    width: 300px;
}   
.images img {
    border-radius: 50%;
    box-shadow: 0 0 10px #D1D9E6;
}


/* Section 1 End */

